import { __decorate } from "tslib";
import { Component, Vue, Prop } from 'vue-property-decorator';
import banner from '@/components/common-components/bannerSlide';
let Home = class Home extends Vue {
    constructor() {
        super(...arguments);
        this.redpackClick = false;
        this.myInfo = {};
        this.weixinInfo = {};
        this.bannerUrl = 'http://img.beijixiong.club:4869/8115a9e1ffaea1d3cba8112c26e1e764';
        // 声明周期钩子
        this.model = { details: '', type: 'type1' };
        this.houseId = '';
        // tab切换配置
        this.selectedLabelDefault = 'unprocessed';
        this.tabBarList = [{
                label: '排行榜',
                value: 'unprocessed'
            }, {
                label: '我的邀请',
                value: 'processed'
            }];
        // 
        this.page = 1;
        this.isShowMore = true;
        this.repairList = [];
    }
    activated() {
        // this.getWeiXinConfig()
        this.$store.commit('SET_SESSION_ID', 'noSession');
        this.pageInit();
    }
    pageInit() {
        console.log('pageinit------------------');
        let query = this.$route.query;
        if (query.openid) {
            this.openid = query.openid;
            this.getActiveRankList();
        }
        else if (query.code) {
            this.getUserInfo();
        }
    }
    validateHandler(result) {
        this.validity = result.validity;
        this.valid = result.valid;
        console.log(result);
        console.log('validity', result.validity, result.valid, result.dirty, result.firstInvalidFieldIndex);
    }
    getUserInfo() {
        let query = this.$route.query;
        if (query.code) {
            const toast = this['$createToast']({
                time: 0,
                txt: '数据获取中...'
            });
            toast.show();
            this.$store.dispatch('getCommodityUserInfo', { code: query.code }).then((response) => {
                if (response.success) {
                    if (response.data.openid) {
                        this.openid = response.data.openid;
                        this.getActiveRankList();
                        if (response.data.weixinInfo) {
                            console.log(response.data.weixinInfo, '0000000000000000000000');
                            this.$set(this, 'weixinInfo', response.data.weixinInfo);
                        }
                        // this.$router.replace({ path: '/login', query: { openid: response.data.openid, jump: '/home' } })
                    }
                }
                toast.hide();
            });
        }
        else {
            console.log('不是微信登陆');
        }
    }
    tabBarChange(value) {
        this.page = 1;
        this.repairList = [];
        this.isShowMore = true;
        this.getActiveRankList();
    }
    get userInfo() {
        return this.$store.state.userInfo;
    }
    getActiveRankList() {
        const toast = this['$createToast']({
            time: 0,
            txt: '数据获取中...'
        });
        toast.show();
        this.$store.dispatch('getActiveRankList', { activeId: 'type1', openid: this.openid, searchType: this.selectedLabelDefault, page: this.page }).then((response) => {
            if (response.success) {
                console.log(response.data.list);
                if (response.data.list.length > 0) {
                    this.repairList = this.repairList.concat(response.data.list);
                    if (response.data.myData) {
                        this.$set(this, 'myInfo', response.data.myData);
                    }
                    if (response.data.list.length < 10) {
                        this.isShowMore = false;
                    }
                    else {
                        this.isShowMore = true;
                    }
                }
                else {
                    this.isShowMore = false;
                }
            }
            toast.hide();
        });
    }
    showMoreClick() {
        this.page++;
        this.getActiveRankList();
    }
    updateRepairState(id) {
        this.$store.dispatch('updateCommodityOrderState', { out_trade_no: id }).then((response) => {
            if (response.success) {
                this['$createDialog']({
                    type: 'alert',
                    content: '处理完成',
                    icon: 'cubeic-ok'
                }).show();
                this.tabBarChange();
            }
        });
    }
    sendredpackClick() {
        if (this.redpackClick) {
            return;
        }
        this.redpackClick = true;
        this.$store.dispatch('sendredpack', { openid: this.openid }).then((response) => {
            if (response.success) {
                this.redpackClick = false;
                this.$set(this.myInfo, 'getredpack', true);
                this['$createDialog']({
                    type: 'alert',
                    content: '红包已发放，注意查收！',
                    icon: 'cubeic-ok'
                }).show();
            }
        });
    }
    getActiveShareCode() {
        const toast = this['$createToast']({
            time: 0,
            txt: '邀请码获取中...',
            mask: true
        });
        toast.show();
        this.$store.dispatch('getActiveShareCode', { openid: this.openid }).then((response) => {
            toast.hide();
            if (response.success) {
                this.redpackClick = false;
                this.$createDialog({
                    type: 'alert',
                    confirmBtn: {
                        text: '我知道了',
                        active: true
                    }
                }, (createElement) => {
                    return [
                        createElement('div', {
                            'class': {
                                'my-title': true
                            },
                            slot: 'title'
                        }, [
                            createElement('p', '邀请同学扫码报名')
                        ]),
                        createElement('img', {
                            'class': {
                                'code-img': true
                            },
                            'attrs': {
                                src: response.data.url
                            },
                            slot: 'content'
                        })
                    ];
                }).show();
            }
        });
    }
    getMoneyText(num) {
        let js = 0;
        if (num <= 3) {
            js = 300;
        }
        else if (3 < num && num <= 10) {
            js = 450;
        }
        else if (num > 10) {
            js = 750;
        }
        return '奖金￥' + num * js + '+';
    }
};
__decorate([
    Prop({ default: 'default value' })
], Home.prototype, "propB", void 0);
Home = __decorate([
    Component({
        components: {
            banner
        },
        filters: {
            getFormatDate(date) {
                return new Date(date).getFullYear() + '年' + (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate() + '号';
            },
            getPhone(item) {
                if (item && item.user_info) {
                    return item.user_info[0].phone;
                }
                else {
                    return '';
                }
            },
            getAddress(item) {
                if (item && item.user_info) {
                    return item.user_info[0].address;
                }
                else {
                    return '';
                }
            }
        }
    })
], Home);
export default Home;
